import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ConversationService } from 'src/services/conversation.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TicketService } from '../../services/ticket.service';
import { ToastrService } from 'ngx-toastr';
import { CrudService } from 'src/app/services/crud.service';
import { requestUrl } from 'src/app/services/adminReqUrl.const';
import { cusRequestUrl } from 'src/app/services/customerReqUrl.const';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { NotificationsService } from 'src/services/notifications.service';
import { UploadFileService } from 'src/app/services/upload-file.service';
import { XorEncryptionService } from 'src/app/services/xor-encryption.service';

@Component({
  selector: 'app-view-ticket',
  templateUrl: './view-ticket.component.html',
  styleUrls: ['./view-ticket.component.scss'],
})
export class ViewTicketComponent implements OnInit {
  isexpandedMsgWindow: any = true;
  @ViewChild('scroll', { read: ElementRef })
  public scroll!: ElementRef<any>;

  tab: any = 'conversion';
  searchKey: any;
  ticketId: any;
  id: any;
  conversationForm: FormGroup;
  conversations: any = [];
  attachments: any = [];
  newFileList: any;
  fileList: any = [];
  ticketDetail: any;
  ticket_status: any;
  formData: FormData = new FormData();
  articleList: any;
  pageSize: number = 10;
  page: number = 1;
  totalArticle: number = 0;
  chatHistory: any;
  userDetail: any = JSON.parse(localStorage.getItem('userdetails') || '{}');
  answerDetail: any;
  articleAnswerView: boolean = false;
  selectedFiles: any;
  fileType: any;
  constructor(
    private _converservice: ConversationService,
    private sanitizer: DomSanitizer,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private router: Router,
    private _ticketservice: TicketService,
    private toastr: ToastrService,
    private crud: CrudService,
    public noti: NotificationsService,
    private uploadService: UploadFileService,
    private xorEncryption:XorEncryptionService,
  ) {
    this.ticketId = this.xorEncryptDecrypt(this.route.snapshot.paramMap.get('ids'));
    this.id = this.xorEncryptDecrypt(this.route.snapshot.paramMap.get('id'));
    this.conversationForm = this.fb.group({
      sender: ['', Validators.required],
      recipient: ['', Validators.required],
      message: [''],
      message_type: ['text'],
      file: [''],
      attachement_name:[''],
    });
  }

  ngOnInit(): void {
    this._converservice.initSocket(this.ticketId);
    this.noti.onMessageUser.subscribe((res: any) => {
      if (res && res.type === 'user_attachment_refresh') {
        if (res.ids == this.ticketId) this.retrieveConversation();
      }
    });
    this.retrieveConversation();   
    // this.noti.firstMsg.subscribe(res => {
    //   if (res) {
    //     this._converservice.initSocket(this.ticketId);
    //     this.retrieveConversation(true);
    //   }
    // });
    this._converservice.onConversation.subscribe((res: any) => {
      //this.conversations.unshift(res);
      console.log(res);
      if (res && res.message) {
        console.log(res);
        res['message'] =
          res?.message_type == 'article'
            ? this.sanitizer.bypassSecurityTrustHtml(res?.message)
            : res.message;
        if (res?.message_type == 'article') {
          res.message =
            res.message.changingThisBreaksApplicationSecurity.replace(
              'SafeValue must use [property]=binding:',
              ''
            );
          res.message = res.message.replace(
            '(see https://g.co/ng/security#xss)',
            ''
          );
        } else {
          res.message = res.message.replace(
            'SafeValue must use [property]=binding:',
            ''
          );
          res.message = res.message.replace(
            '(see https://g.co/ng/security#xss)',
            ''
          );
        }
        this.conversations.push(res);
      } else if (res && res.file) {
        this.conversations.push(res);
      }
      this.scrollBottom();
    });
    this.getTicketDetail();
  }

  navigateToEditTicket(ticket:any) {
    this.router.navigateByUrl(`/support/create/${this.xorEncryption.xorEncryptDecrypt(ticket.ids)}/${this.xorEncryption.xorEncryptDecrypt(ticket.id)}`);
  }

  xorEncryptDecrypt(text:string | null, key = "safeViezu") {
    if(text === null) return text;

    if(typeof text == 'number') {
      text = `${text}`;
    }
    return text.split("").map((char, i) => 
        String.fromCharCode(char.charCodeAt(0) ^ key.charCodeAt(i % key.length))
    ).join("");
  }

  getTicketDetail() {
    this.crud
      .getRequest(`${requestUrl.getTicketDetail}${this.id}`)
      .then((res: any) => {
        if (res) {
          this.ticketDetail = res.data;
          console.log(this.ticketDetail?.file_request?.request_id || !this.ticketDetail?.request_id,'condition for car icon');
          
          this.ticket_status =
            res.ticket_status != null ? res.ticket_status.team_status : 'New';
        }
      });
  }


  decodeFileNameFromUrl(name:string): string {
    if(!name) return '';
    return decodeURIComponent(name.split('/')[name.split('/').length-1]);
  }

  selectFile(event: any) {
    this.selectedFiles = event.target.files;
    const file = this.selectedFiles.item(0);
    this.uploadService.uploadFile(file, (cb: any) => {
      this.conversationForm.controls['file'].setValue(cb);
      this.conversationForm.controls['message_type'].setValue('file');
      this.conversationForm.controls['attachement_name'].setValue(file.name);
      this.fileType = cb.split('.');
      this.fileType = this.fileType[this.fileType.length - 1];
    });
  }

  conversationFormSubmit() {
    let data = this.conversationForm.value;
    data.message = data.message.replace(
      'SafeValue must use [property]=binding:',
      ''
    );
    data.message = data.message.replace(
      '(see https://g.co/ng/security#xss)',
      ''
    );
    console.log(data, 'data');
    if (
      this.conversationForm.controls['file'].value != '' ||
      this.conversationForm.controls['message'].value != ''
    ) {
      this._converservice.sendMessage(JSON.stringify(data));
      this.conversationForm.patchValue({ message: '' });
      this.conversationForm.patchValue({ file: '' });
      this.conversationForm.patchValue({ message_type: 'text' });
    }
  }

  retrieveConversation(createFirstMsg?: boolean) {
    this._ticketservice.getConversation(this.ticketId).subscribe(
      (res: any) => {
        if (res) {
          this.conversations = res.data.results.reverse();
          if (this.conversations.length > 0) {
            this.conversations.forEach((element: any) => {
              if (element) {
                element['message'] = this.sanitizer.bypassSecurityTrustHtml(
                  element.message
                );
              }
            });
          }
          this.conversationForm.patchValue({
            sender: res.sender,
            recipient: res.recipient,
          });
          // if (createFirstMsg) {
          //   debugger
          //   this.conversationForm.patchValue({ 'message': this.noti.getFirstMsg() })
          //   this.conversationFormSubmit();
          // }
          setTimeout(() => {
            this.scrollBottom();
          },200)
        }
      },
      (err: any) => {}
    );
  }
  getKnowledgeBaseDetail() {
    if (this.searchKey) {
      this.crud
        .getRequest(`${cusRequestUrl.getArticleList}?search=${this.searchKey}`)
        .then((res: any) => {
          if (res) {
            this.articleList = res.data.results;
            this.totalArticle = res.data.count;
          }
        });
    } else {
      this.crud
        .getRequest(
          `${cusRequestUrl.getArticleList}?page=${this.page}&page_size=${this.pageSize}`
        )
        .then((res: any) => {
          if (res) {
            this.articleList = res.data.results;
            this.totalArticle = res.data.count;
          }
        });
    }
  }
  handlePageChange(event: number): void {
    this.page = event;
    this.getKnowledgeBaseDetail();
  }

  getAttachments() {
    this.tab = 'attachment';
    this._ticketservice.getAttachments(this.ticketId).subscribe(
      (res: any) => {
        this.attachments = res.data;
      },
      (err: any) => {
        //this.toastr.error(err.error.detail,'', { timeOut: 4000 });
      }
    );
  }

  onFileChange(event: any) {
    this.newFileList = Array.from(event.target.files);
    this.formData.append('attachement_name', this.newFileList[0].name);
    this.formData.append('attachement_type', '2');
    if (this.newFileList.length > 0) {
      for (let file of this.newFileList) {
        let detail = {
          name: file.name,
          type: file.type,
          size: file.size / 1000 + 'KB',
          file: file,
        };
        this.fileList.push(detail);
        this.formData.append('files', file, file.name);
      }
    }

    this._ticketservice.saveAttachments(this.ticketId, this.formData).subscribe(
      (res: any) => {
        this.toastr.success(res.message, '', { timeOut: 4000 });
        this.getAttachments();
      },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      }
    );
  }

  readArticle(ques: any) {
    var text = ques.changingThisBreaksApplicationSecurity.split('?')[1];
    text = ques.changingThisBreaksApplicationSecurity
      .split('?')[1]
      .slice(0, text.length - 4);

    const url = this.router.serializeUrl(
      this.router.createUrlTree([`/knowledgebase`], {
        queryParams: { ques: text },
      })
    );
    window.open(url, '_blank');
  }
  getArticleDetail(id: string, type?: string) {
    this.crud
      .getRequest(`${requestUrl.getArticleDetail}${id}`)
      .then((res: any) => {
        this.answerDetail = res;
        this.answerDetail.answer = this.sanitizer.bypassSecurityTrustHtml(
          this.answerDetail?.answer
        );
        this.articleAnswerView = true;
      });
  }
  ngOnDestroy() {
    this._converservice.closeSocket();
  }


  scrollBottom() {
    this.scroll.nativeElement.scrollTop =
      this.scroll.nativeElement.scrollHeight;
  }

  scrollToTop() {
    this.scroll.nativeElement.scrollTop = 0;
  }

  expandMsgWindow() {
    this.isexpandedMsgWindow = !this.isexpandedMsgWindow;
  }


  getChatHistory() {
    this.crud
      .getRequest(
        `${requestUrl.getChatHistory}${this.ticketId}?page=${this.page}&page_size=${500}`
      )
      .then((res: any) => {
        this.chatHistory = res.data.results;
      });
  }


  vehDetail: any;
  getVehicleDetail() {
    if (this.ticketDetail?.request_id) {
      this.crud
        .getRequest(
          `file/service/request/detail/${this.ticketDetail?.request_id.replace(
            'FR',
            ''
          )}`
        )
        .then((res: any) => {
          this.vehDetail = res;
          if("other_fields" in this.vehDetail && this.vehDetail.other_fields) {
            this.vehDetail.other_fields = this.vehDetail.other_fields.replaceAll('_',' ').replaceAll(',',', ');
          }
        });
    } else {
      this.vehDetail = this.ticketDetail?.vehicle_details;
    }
  }
}
