  updateTicket() {
    if (this.ticketCreateForm.valid) {
      this.body = {};
      // this.formData = new FormData();
      // this.formData.append('files', this.fileDetails, this.fileDetails.name);
      if (this.vehicleTypes) {
        this.vehicleTypes.forEach((element: any) => {
          if (this.vehicleInformation.controls['vehicle_type'].value == element.id) {
            
            this.other_fields = [];
            if((element.name == 'Other')) {
              this.other_fields.push("Vehicle Type");
            }
            this.body['vehicle_type']= (element.name == 'Other') ? this.vehicleInformation.controls['other_vehicle_type'].value : element.name;
          }
        });
      }
      if (this.vehicleBrandList) {
        this.vehicleBrandList.forEach((element: any) => {
          if (this.vehicleInformation.controls['vehicle_make'].value == element.id) {
            if((element.brand_name == 'Other')) {
              this.other_fields.push("Vehicle Make");
            }

            this.body['vehicle_make']= (element.brand_name == 'Other') ? this.vehicleInformation.controls['other_vehicle_make'].value : element.brand_name;
          }
        });
      }

      if (this.modelList) {
        this.modelList.forEach((element: any) => {
          if (this.vehicleInformation.controls['model'].value == element.id) {

            if((element.model_name == 'Other')) {
              this.other_fields.push("Model");
            }
            this.body['model']= (element.model_name == 'Other') ? this.vehicleInformation.controls['other_model'].value : element.model_name;
          }
        });
      }
      var ecuBrandName = '';
      if (this.editEcuBrandVesrsion == false && this.ecuBrandList) {
        this.ecuBrandList.forEach((element: any) => {
          if (element.id == this.ecuInformation.value.ecu_brand)
            ecuBrandName = element.ecu_brand
        });
      }
      else {
        ecuBrandName = this.ecuInformation.value.ecu_brand;
      }
      let engineSizeValue = ''
      if (this.engineInformation.controls['engine_size'].value) {
        engineSizeValue = `${this.engineInformation.controls['engine_size'].value}.${this.engineInformation.controls['engine_size_deci'].value}`
      }
      else {
        engineSizeValue = `${this.engineInformation.controls['engine_size'].value}.0`

      }
      this.body['ecu_brand'] = this.showEcuBrand ? this.ecuInformation.value.other_ecu_brand : ecuBrandName;
      if(this.showEcuBrand) {
        this.other_fields.push("ECU Brand");
      }

      this.body['ecu_version'] = this.showEcuVersion ? this.ecuInformation.value.other_ecu_version : this.ecuInformation.value.ecu_version;
      if(this.showEcuVersion) {
        this.other_fields.push("ECU Version");
      }

      this.body['vehicle_registration'] = this.vehicleInformation.controls['vehicle_registration'].value;
      this.body['vehicle_year'] = this.vehicleInformation.controls['vehicle_year'].value;
      this.body['vehicle_VIN'] = this.vehicleInformation.controls['vehicle_VIN'].value;
      this.body['engine_size'] = engineSizeValue;

      this.body['tuning_tool_used'] = this.vehicleInformation.controls['tuning_tool_used'].value == 'Other' ? this.vehicleInformation.controls['other_tuning_tool_used'].value : this.vehicleInformation.controls['tuning_tool_used'].value;
      if(this.vehicleInformation.controls['tuning_tool_used'].value == 'Other') {
        this.other_fields.push("Tuning Tool Used");
      }

      this.body['transmission'] = this.engineInformation.controls['transmission'].value == 'Other' ? this.engineInformation.controls['other_transmission'].value : this.engineInformation.controls['transmission'].value;
      if(this.engineInformation.controls['transmission'].value == 'Other') {
        this.other_fields.push("Transmission");
      }

      this.body['fuel_type'] = this.engineInformation.controls['fuel_type'].value == 'Other' ? this.engineInformation.controls['other_fuel_type'].value : this.engineInformation.controls['fuel_type'].value;
      if(this.engineInformation.controls['fuel_type'].value == 'Other') {
        this.other_fields.push("Fuel Type");
      }

      this.body['mileage_type'] = this.engineInformation.controls['mileage_type'].value;
      this.body['mileage'] = this.engineInformation.controls['mileage'].value;

      this.body['control_unit'] = this.ecuInformation.controls['control_unit'].value == 'Other' ? this.ecuInformation.controls['other_control_unit'].value : this.ecuInformation.controls['control_unit'].value;
      if(this.ecuInformation.controls['control_unit'].value == 'Other') {
        this.other_fields.push("Control Unit");
      }

      this.body['tuning_required'] = this.additionalInformation.controls['tuning_required'].value;
      this.body['country'] = this.additionalInformation.controls['country'].value;
      this.body['file_type'] = this.vehicleInformation.controls['file_type'].value;
      this.body['additional_info'] = this.additionalInformation.controls['additional_info'].value;
      if(this.additionalInformation.controls['additional_info'].value) {
        this.other_fields.push("additional_info");
      }

      this.body['additional_function']= this.additionalInformation.controls['additional_function'].value == 'Other' ? this.additionalInformation.controls['other_additional_function'].value : this.additionalInformation.controls['additional_function'].value;
      if(this.additionalInformation.controls['additional_function'].value == 'Other') {
        this.other_fields.push("Additional function");
      }
      
      for (var key in this.body) {
        if (this.body.hasOwnProperty(key)) {
          // console.log(key + ': ' + this.body[key]);
          if (key == 'mileage') { }
          else if (this.body[key].includes("Select")) {
            this.body[key] = "";
          }
        }
      }
      if (this.body['engine_size'] == '.0')
        this.body['engine_size'] = '';

      // join the other_fields array into a string
      this.body['other_fields'] = this.other_fields.join(', ');
      this.other_fields = [];

      // If Customer select "in house booking" then show warning 
      // if(this.ticketCreateForm.value.category == 14 && this.isVehicleDetailsFormValid()) {
      //   this.toastr.error("Please complete the vehicle details in full for an in house booking.");
      //   return;
      // }


      this.formData.append('subject', this.ticketCreateForm.value.subject);
      this.formData.append('category', this.ticketCreateForm.value.category);
      this.formData.append('description', this.ticketCreateForm.value.description);


      this.formData.append('vehicle_details', JSON.stringify(this.body));

      this._ticketservice.updateTicketFromCustomer(this.formData, this.ticketHashID).subscribe((res: any) => {
        this.toastr.success('Ticket created successfully.', '', { timeOut: 4000 });
        // this.noti.setFirstMsg(this.ticketCreateForm.value.description,res.ids);
        // this.noti.updateFirstMessage(true);
        this.router.navigate(['support']);
      },
        (err: any) => {
          this.toastr.error(err.error.detail, '', { timeOut: 4000 });
          this.formData = new FormData();
          this.formData.append('files', this.fileDetails, this.fileDetails.name);
        })
    } else {
      this.markAllControlsAsTouched(this.ticketCreateForm)
    }
  }
