  markAllControlsAsTouched(formGroup: FormGroup) {
    Object.values(formGroup.controls).forEach(control => {
      control.markAsTouched();

      if (control instanceof FormGroup) {
        this.markAllControlsAsTouched(control);
      }
    });
  }

  isDTCSelected = false;
  checkForSelectedDTC(i:number):void {
    if(!Number.isInteger(i)) return;

    if(this.selectAdditionalFun[i] && this.additionalFunctionList[i] == 'DTC') 
    {
      this.toastr.warning("Please put the full Fault Code and description in the additional information box below");
      this.isDTCSelected = true;
    }

    if(!this.selectAdditionalFun[i] && this.additionalFunctionList[i] == 'DTC') 
    {
      this.isDTCSelected = false;
    }
  }


  fileDetails:any;
  onFileChange(event: any) {
    this.newFileList = Array.from(event.target.files);
    if (this.newFileList.length > 0) {
      for (let file of this.newFileList) {
        let detail = {
          "name": file.name,
          "type": file.type,
          "size": (file.size / 1000) + "KB",
          "file": file,
        }
        this.fileList.push(detail);
        this.fileDetails = file;
        this.formData.append('files', file, file.name);
      }
    }
  }

  removeFile(index: any) {
    this.fileList = [];
    this.newFileList.splice(index, 1);
    this.formData.delete('files');
    if (this.newFileList.length > 0) {
      for (let file of this.newFileList) {
        let detail = {
          "name": file.name,
          "type": file.type,
          "size": (file.size / 1000) + "KB",
          "file": file,
        }
        this.fileList.push(detail);
        this.formData.append('files', file, file.name);
      }
    }
  }

  previewPdfModal(index: any) {
    var reader = new FileReader();
    reader.onload = (event: any) => {
      this.previewSrc = event.target.result;
    }
    reader.readAsDataURL(this.fileList[index].file);
  }


  getTicketCategory() {
    this.crud.getRequest(cusRequestUrl.getTicketCategory).then((res: any) => {
      if (res) {
        const ticketCategoryList = res.data.results;
        this.ticketCategories = ticketCategoryList.filter((element: any) => {
          if (element.name != "Manual Handle" && element.name != "Processing") {
            return element;
          }
        });
        this.ticketCreateForm.patchValue({ 'category': this.ticketCategories[0].id });

        if(this.ticketID) {
          this.getTicketDetail();
        }
      }
    });
  }
  moveCountryOnTop(name: string) {
    const index = this.countryData.findIndex((item: any) => item.country === name);
    if (index !== -1) {
      const removedObject = this.countryData.splice(index, 1)[0];
      this.countryData.unshift(removedObject);
    }
  }

  moveOtherInLast(arr: any, key: any) {
    let otherIndex = arr.indexOf(key);
    // Move "Other" to the last index
    if (otherIndex !== -1) {
      arr.splice(otherIndex, 1);  // Remove "Other" from its current position
      arr.push("Other");  // Add "Other" to the end of the array
    }
  }
  sortEle(arr: any) {
    return arr.sort((a: any, b: any) => {
      return a.toLowerCase().localeCompare(b.toLowerCase());
    });
  }
  sort(array: any, key: any) {
    return array.sort((a: any, b: any) => {
      var nameA = a[key].toUpperCase();
      var nameB = b[key].toUpperCase();
      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    });
  }
