import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  baseUrl = environment.baseUrl;
  constructor(private _http:HttpClient) 
  {


  }
  
  getCustomers(params:any)
  {
    return this._http.get(this.baseUrl+"/api/v1/admin/customer", { params });
  }

  getCustomerByUuid(id:any)
  {
    return this._http.get(`${this.baseUrl}/api/v1/admin/customer/${id}`);
  }
  createFileServiceRequest(data:any) {
    return this._http.post(`${this.baseUrl}/api/v1/file/service/request`,data);
  }
  getFileFormData() {
    return this._http.get(`${this.baseUrl}/api/v1/file/service/get/file_form_data`);
  }

}
