  ngOnInit(): void {
    console.log('build update');
    
    this.retrieveTickets();
    this.getTicketCategory();
    this.getCountByCategory();
    this.handleLiveTicketUpdates()
    // this.formatDate("a");
  }
  handleLiveTicketUpdates() {
    this.noti.onTicketUpdats.subscribe((res: any) => {
      if (!res || !this.tickets?.length) return;

      const ticketToBeUpdated = this.tickets.find((el: any) => el.ids == res.ticket_id)
      if (!ticketToBeUpdated) return;


      ticketToBeUpdated.conversation_count = res.ticket_conversation;
      ticketToBeUpdated.updated_at = res.ticket_modified_time;

      // Update Status
      ticketToBeUpdated.ticket_status.team_status = res.ticket_status;
      ticketToBeUpdated.ticket_status.display_user_status = res.ticket_status;

      // Update Assigned to
      ticketToBeUpdated.assigned_to.first_name = res.ticket_assigned;
      ticketToBeUpdated.assigned_to.last_name = ' '

      console.log(ticketToBeUpdated, 'updated ticket');

    })
  }

  navigateToTicket(tkID:string, ID:string) {
    this.router.navigateByUrl(`/support/view-ticket/${this.xorEncryption.xorEncryptDecrypt(tkID)}/${this.xorEncryption.xorEncryptDecrypt(ID)}`);
    // [routerLink]="['/support/view-ticket', ticket.ids, ticket.id]"
  }

  navigateToEditTicket(ticket:any) {
    this.router.navigateByUrl(`/support/create/${this.xorEncryption.xorEncryptDecrypt(ticket.ids)}/${this.xorEncryption.xorEncryptDecrypt(ticket.id)}`);
  }



