  getTicketCategory() {
    this.crud.getRequest(`${requestUrl.getTicketCategory}`).then((res: any) => {
      if (res)
        this.ticketCategories = res.data.results;
      this.ticketCategories = this.ticketCategories.filter((element: any) => {
        if (element.name != "Manual Handle") {
          return element;
        }
      });
    })
  }

  getTicketDetail(data: any) {
    let sharedata = {
      'assigned_to_name': data.assigned_to?.first_name,
      'assigned_to_email': data.assigned_to?.email,
      'status': data.ticket_status != null ? data.ticket_status.team_status : 'New'
    };
    localStorage.setItem('ticketsharedata', JSON.stringify(sharedata));
    
  }

  getRequestParams(searchTitle: string, page: number, pageSize: number, status: any, category: any, created_at: any): any {
    let params: any = {};
    if (searchTitle) {
      params[`search`] = searchTitle;
    }
    if (page) {
      params[`page`] = page;
    }
    if (pageSize) {
      params[`page_size`] = pageSize;
    }

    if (status) {
      params[`status`] = status;
    }
    if (category) {
      params[`category`] = category;
    }
    if (created_at) {
      params[`created_at`] = created_at;
    }
    return params;
  }
  // admin/ticket/search?search=Transit Custom

  searchTicketResult() {
    if (this.searchKey.length > 0) {
      this.crud.getRequest(`customer/ticket/search?search=${this.searchKey}`)
        .then((res: any) => {
          const data = ("data" in res) ? res.data : res;
          this.tickets = data?.results;
          this.totalTicket = data?.count;
          this.showError = this.tickets.length == 0 ? true : false;
        }
        )
    } else {
      this.retrieveTickets();
    }
  }
  retrieveTickets(field?: string, order?: string): void {
    const params = this.getRequestParams(this.title, this.page, this.pageSize, this.status, this.category, this.created_at);
    const filter = this.searchKey ? `search?search=${this.searchKey}` : ''

    if (field) {
      this.sortField = field;
      this.sortOrder = order;
    }
    if (this.sortField) {
      params[`field`] = this.sortField;
      params[`order_in`] = this.sortOrder;
    }
    this._ticketservice.getTicket(params, filter)
      .subscribe((res: any) => {
        this.tickets = res?.results;
        this.totalTicket = res?.count;
        this.showError = this.tickets.length == 0 ? true : false;
      },
        (err: any) => {
          this.toastr.error(err.error.detail, '', { timeOut: 4000 });
        }
      )

  }

  getCountByCategory() {
    this.crud.get(`admin/ticket/status/count`)
      .subscribe((res: any) => {
        if (res) {
          this.filterTicketResult(res);
        }
      })
  }

  filterTicketResult(res: any) {
    //this.ticketStatus = res.count_by_category;
    if (this.ticketStatus.length == 0) {
      res.count_by_category.forEach((value: any, index: any) => {

        if (value.id != null) {
          this.ticketStatus.push(value);
        }
        else {
          let nulldata = { "id": null, "count": value.count, "ticket_status": null };
          this.ticketStatus.unshift(nulldata);
        }
      });
    }
  }
