  handlePageChange(event: number): void {
    this.page = event;
    this.retrieveTickets();
  }

  onKeyUp(x: any) {
    this.title = x;
    this.page = 1;
    this.retrieveTickets();
  }

  filterTicketFormSubmit() {
    this.page = 1;
    this.category = this.filterTicketForm.value.category;
    this.status = this.filterTicketForm.value.status;
    this.created_at = this.filterTicketForm.value.created_at;
    this.getTicketByFilter();

    document.getElementById('closefilterconvas')?.click();
  }
  getTicketByFilter() {
    let params: any = {};
    if (this.status) {
      params[`status`] = this.status;
    }
    if (this.category) {
      params[`category`] = this.category;
    }
    if (this.created_at) {
      params[`created_at`] = this.created_at;
    }
    this._ticketservice.getTicketOnFilter(params).subscribe((res: any) => {
      // this.filterTicketResult(res);
      this.tickets = res?.data?.results;
      this.totalTicket = res?.data?.count;
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      })
  }

  filterByStatus() {
    this.status = this.ticketIds.toString();
    this.page = 1;
    if (this.status)
      this.getTicketFilterByTicketStatus();
    else
      this.retrieveTickets();
    document.getElementById('closefiltermodal')?.click();
  }
  getTicketFilterByTicketStatus() {
    this.crud.getRequest(`admin/ticket/filter_by_status?status=${this.status}`).then((res: any) => {
      // this.filterTicketResult(res);
      this.tickets = res?.data?.results;
      this.totalTicket = res?.data?.count;
    })
  }


  onChangeTicketStatus(tkid: any) {
    if (this.ticketIds.includes(tkid)) {
      const index = this.ticketIds.indexOf(tkid);
      if (index > -1) {
        this.ticketIds.splice(index, 1);
      }
    }
    else {
      this.ticketIds.push(tkid);
    }
  }
  truncateText(text: string, maxLength: number): string {
    if (text?.trim().length > maxLength) {
      return text.substring(0, maxLength) + '...';
    }
    return text;
  }


  isStandaloneTicket = false;
  getVehicleDetail(ticketDetails:any) {
    
    this.isStandaloneTicket = !ticketDetails?.request_id;

    if (!ticketDetails?.request_id) { 
      this.vehicleDetails = ticketDetails?.vehicle_details;
      return;
    }
    
    this.crud
      .getRequest(
        `file/service/request/detail/${ticketDetails?.request_id.replace(
          'FR',
          ''
        )}`
      )
      .then((res: any) => {
        this.vehicleDetails = res;
        if("other_fields" in this.vehicleDetails && this.vehicleDetails.other_fields) {
          this.vehicleDetails.other_fields = this.vehicleDetails.other_fields.replaceAll('_',' ').replaceAll(',',', ');
        }
      });
  }

